import React, { Component } from 'react';
import { crisp } from 'cinnamon-sugar';
import names from './names';
import quotes from './quotes';
import icons from './icons';
import themes from './themes';
import ButterToast from 'butter-toast';
import './style.css';

const rand = (arr) => arr[Math.floor(Math.random()*arr.length)] || arr[0];

const raiseOne = (name, extra = {}) => {
    const showIcon = (Math.ceil(Math.random()*2))%2 === 1,
        quote = rand(quotes),
        icon = rand(icons);

    ButterToast.raise(crisp({
        toastTimeout: extra.randTimeout ? Math.round(Math.random() * 4000) + 3000 : 5000,
        name,
        message: quote,
        title: rand(['Success!', 'Error', 'Danger!', 'FYI', 'Victory!', 'You\'ve got mail!', 'Oopsie']),
        theme: rand(themes)
    }), extra);

};

const wow = (rand, extra) => {
    names.forEach((name) => {
        const num = Math.round(Math.random()*2);
        for (let index = 0; index < num; index++) {
            raiseOne(name, extra);
        }
    });
}

const crazy = () => wow(true, {
    randTimeout: true
});

const raiseSticky = () => {
    raiseOne(rand(names), {
        sticky: true,
        dismissOnClick: true
    })
}

class App extends Component {

    render() {
        return (
            <div className="toast-app">
                <h1 className="title">BUTTER TOAST</h1>
                <span className="sub">Smooth toast notification system for react apps</span>
                <div>
                    <a href="#!" className="btn-show" onClick={wow}>
                        Show me what you got
                    </a>
                    <a href="#!" className="btn-show" onClick={raiseSticky}>
                        And this one is sticky
                    </a>
                    <a href="#!" className="btn-show" onClick={crazy}>
                        Random Timeouts
                    </a>
                </div>
                <div>
                    <a href="https://github.com/ealush/butter-toast">Github</a> OR <a href="https://www.npmjs.com/package/butter-toast">NPM</a>
                </div>
                <ButterToast trayPosition="top-left" name="top-left"/>
                <ButterToast trayPosition="top-center" name="top-center"/>
                <ButterToast trayPosition="top-right" name="top-right"/>
                <ButterToast trayPosition="bottom-left" name="bottom-left"/>
                <ButterToast trayPosition="bottom-center" name="bottom-center"/>
                <ButterToast trayPosition="bottom-right" name="bottom-right"/>
                <a href="https://github.com/ealush/butter-toast"><img className="fork" src="https://camo.githubusercontent.com/38ef81f8aca64bb9a64448d0d70f1308ef5341ab/68747470733a2f2f73332e616d617a6f6e6177732e636f6d2f6769746875622f726962626f6e732f666f726b6d655f72696768745f6461726b626c75655f3132313632312e706e67" alt="Fork me on GitHub" data-canonical-src="https://s3.amazonaws.com/github/ribbons/forkme_right_darkblue_121621.png"/></a>
            </div>
        );
    }
}

export default App;



